import React, { Component } from "react";
import Typist from "react-typist";

export class InfoHolder extends Component {
    render() {
        return (
            <div className="container" style={{ lineHeight: "2" }}>
                <div>I am developer with can-do attitude</div>
                <Typist cursor={{ show: false }} avgTypingDelay={50}>
                    <div>
                        Constantly learning to solve bigger and better problem
                    </div>
                </Typist>
            </div>
        );
    }
}

export default InfoHolder;
